import React, { useState } from "react";

function KontaktForm() {
  const [status, setStatus] = useState("");

  const handleSubmit = (e) => {
    e.preventDefault();
    setStatus("Wiadomo wysana! (Zaimplementuj EmailJS lub wasny backend.)");
  };

  return (
    <section id="kontakt">
      <h2>Kontakt</h2>
      <form onSubmit={handleSubmit}>
        <input required name="name" placeholder="Imi" />
        <input required type="email" name="email" placeholder="Adres e-mail" />
        <textarea required name="message" placeholder="Wiadomo"></textarea>
        <button type="submit">Wylij</button>
      </form>
      <div>{status}</div>
      <p>
        <strong>Telefon:</strong> 123-456-789 <br />
        <strong>Email:</strong> kontakt@twojastrona.pl
      </p>
    </section>
  );
}

export default KontaktForm;
